.header {
  display: flex;
  /* align-content:space-between; */
  justify-content: space-between;
  padding: 10px;
  margin: 10px;
  border: 1px solid black;
}
.logo {
  height: 100px;
}
.nav-list {
  margin-top: 20px;
}
.nav-list ul {
  display: flex;
  gap: 30px;
  list-style-type: none;
  padding: 10px;
  margin: 10px;
  font-size: 20px;
}
.search {
  padding: 20px;
}
 .res-card {
  width: 200px;
 
  margin: 2px; 
  padding: 5px; 
}  

.res-card:hover {
  border: 1px solid black;
  cursor: pointer;
}
.res-logo {
  width: 100%;
}


.res-container{
    display: flex;
    flex-wrap: wrap;
 
}

.res-btn{
  margin: 10px;
  cursor: pointer;
}
